/* RESET */
* {
    margin: 0; 
    padding: 0;
  }
  ul,ol,li {
    list-style: none;
  }
  a {
    text-decoration: none;
    color: #333;
  }
  body {
    background: #efefef;
    font-family: 'Gowun Batang', serif;
  }
  
  /* COMMON  */
  .inner {
    padding: 60px;
  }
  
  /* HEADER */
  .header {
    position: relative;
    max-width: 400px; 
    width: 23%;
    height: 100%;
    background: url(../assets/hd-bg.png);
    position: fixed;
    top: 0;
    left: 0;
    background-size: cover;
  }
  .header__top {
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
  }
  .header__top .header__title {
    position: relative;
    font: 40px/1  'Gowun Batang', serif;
    color: #fff;
    letter-spacing: 1px;
  }
  .header__top .header__title::after {
    content: '';
    position: absolute;
    margin-top: 24px;
    right: 0;
    display: block;
    border-bottom: 1px solid #fff;
    height: 1px;
    width:100%;
  }
  .header__top .header__title a {
    display: block;
    color: #fff;
    margin-top: 12px;
    line-height: 1.5;
  }
  .header__top .header__title span {
    font-size: 16px;
  }
  .header__top .header__user {
    margin-top: 76px;
  }
  .header__top .header__user li {
    text-align: right;
  }
  .header__top .header__user li a {
    color: #fff;
    font-size: 14px;
    display: block;
  }
  .header__top .header__user li:first-of-type a {
    margin-bottom: 24px;
  }
  .header .header__sns {
      position: absolute;
      left: 0px; 
      bottom: 0;
      width: 100%;
      height: 50px;
      background-color: #391519e6;
      display: flex; 
      justify-content: space-around;
      align-items: center;
  }
  .header .header__sns li {
      padding-right: 10px;
  }
  .header .header__sns li a {
      font-size: 20px; color: #fff;
      opacity: 0.4; transition: 0.6s;
  }
  .header .header__sns li:hover a {opacity: 0.9;} 
  .header .header__sns li a span {
      display: inline-block;
      font-family:'Lobster', cursive;
      opacity: 0.8; transform: scale(0.7) translateX(-10px);
  }
  .main {
      margin-left: 400px;
      box-sizing: border-box;
      padding: 20px;
  }
  .main .main__sort {
    width: 100%; 
    margin-bottom: 10px; 
    margin-right: 20px;
  }
  .main .main__sort::after {
    content: ""; 
    display: block; 
    clear: both;
  }
  .main .main__sort li {
    float: right; 
    margin-right: 10px; 
    width: 100px; 
    height: 26px;
  }
  main .main__sort li a {
      display: block; 
      width: 100%; height: 100%; 
      font:  17px/26px 'Gowun Batang', serif;
      color: #999; letter-spacing: 1px;
      text-align: center;
  }
  .main .main__sort li.on a {
      color: #333;
      text-decoration: underline;
  }
  .main .main__content {width: 100%;}
  .main .main__content::after {
      content: ""; display: block; clear: both;
  }
  .main .main__content article {
      width: 20%; float: left;
      box-sizing: border-box;
      padding: 12px;
  }
  .main .main__content article div {
    display: block;
    width: 100%; height: 100%;
    background: #fff; border-radius: 4px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
    box-sizing: border-box; padding: 14px;
    transition: 0.6s;
  }
  .main .main__content article div:hover {opacity: 0.6;}
  .main .main__content article div img {
      width: 100%; 
  }
  .main .main__content article div h2 {
      margin-top: 15px;
      font: bold 15px/1 'Gowun Batang', serif;
      color: #333; letter-spacing: 1px;
      margin-bottom: 5px;
      display: block; white-space: nowrap;
      overflow: hidden; text-overflow: ellipsis;
  }
  .main .main__content article div p {
      font: 12px/1.2 'Gowun Batang', serif;
      color: #555;
      margin-bottom: 15px;
  }
  .main .main__content article div p span {
      padding-right: 18px;
      position: relative;
  }
  .main .main__content article div p span::before {
      content: ""; display: block;
      width: 1px; height: 10px;
      background: #ccc;
      position: absolute;
      left: 31px; top: 4px;
  }
  
  
  @media screen and (max-width:1599px) {
      .header {
        max-width: 350px;
      }
      .main {
        margin-left: 350px;
      }
      .main__content article {
        width: 25%;
      }
  }
  
  @media screen and (max-width:1199px) {
      header.header {
          position: relative; 
          width: 100%; 
          max-width: 100%;
          height: 80px;
      }
      .header .header__title {
          font: bold 16px/1 'Source Serif Pro', serif;
          transform: translateY(-50%);
      }
      .header .header__title::after {
        display: none;
      }
      .header .header__sns {
          position: absolute;
          left: 85%; 
          top: 50%;
          transform: translateY(-50%);
          width:fit-content; 
          height: fit-content;
          background-color: rgba(0, 0, 0, 0);
          display: flex; 
          justify-content: space-around;
          align-items: center;
      }
      .header .header__sns li {
        padding-right: 20px;
      }
      .header .header__sns li a {
        font-size: 20px;
      }
      .header .header__sns li a span {
        display: none;
      }
      .main {
        width: 100%; 
        margin-left: 0;
      }
      .main section article {
        width: 25%;
      }
  }
  
  @media screen and (max-width:899px) {
      .main .main__content article {
        width: 33.3333%;
      }
  }
  
  
  @media screen and (max-width:679px) {
      .header .main__sort {
        left: 70%;
      }
      .main .main__content article {
        width: 50%;
      }
  }
  
  @media screen and (max-width:539px) {
      .header .main__sort {
        display: none;
      }
      .main .main__sort {
        margin-right: 0; 
        box-sizing: border-box; 
        padding: 0 13px;
      }
      .main .main__sort li {
        margin-right: 2%; 
        width: 32%;
      }
      .main .main__sort li:nth-of-type(1) {
        margin-right: 0;
      }
      .main .main__content article {
        width: 100%;
      }
  }